
import {BrowserRouter, Route, Router, Routes} from "react-router-dom"
import HomeScreen from "./screens/HomeScreen";
import ProductScreen from "./screens/ProductScreen";
import Navbar from 'react-bootstrap/Navbar';
import Container from 'react-bootstrap/Container';
import {LinkContainer} from 'react-router-bootstrap';
import { Badge ,Button,Nav, NavDropdown } from "react-bootstrap";
import { ToastContainer } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css'
import { useContext, useEffect, useState } from "react";
import axios from "axios";
import { toast } from "react-toastify";
import { Store } from "./Store";
import { Link } from "react-router-dom";
import ScreenCart from "./screens/ScreenCart";
import SigninScreen from "./screens/SigninScreen";
import ShoppingAddressScreen from "./screens/ShoppingAddressScreen";
import SignupScreen from "./screens/SignupScreen";
import PaymentMethodScreen from "./screens/PaymentMethodScreen";
import PlaceOrderScreen from "./screens/PlaceOrderScreen";
import OrderScreen from "./screens/OrderScreen";
import OrderHistoryScreen from "./screens/OrderHistoryScreen";
import ProfileScreen from "./screens/ProfileScreen";
import { getError } from "./utils";
import SearchBox from "./screens/components/SearchBox";
import SearchScreen from "./screens/SearchScreen";
import ProtectedRoutes from "./screens/components/ProtectedRoutes";
import AdminRoutes from "./screens/components/adminRoutes";
import DashboardScreen from "./screens/DashboardScreen";
import ProductListScreen from "./screens/ProductListScreen";
import { ProductEditScreen } from "./screens/ProductEditScreen";
import { OrderListScreen } from "./screens/OrderListScreen";
function App() {
  const {state , dispatch :ctxDispatch} =useContext(Store);
  const {cart, userInfo} = state;
  console.log("state");
  const [sidebarIsOpen, setSidebarIsOpen] = useState(false);
  const [categories, setCategories] = useState([]);
  const signoutHandler= ()=>{
    ctxDispatch({type:'USER_SIGNOUT'});
    localStorage.removeItem('userInfo');
    localStorage.removeItem('shippingAddress');
    localStorage.removeItem('paymentMethod');
    window.location.href='/signin';

  }
  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const { data } = await axios.get(`/api/products/categories`);
        setCategories(data);
      } catch (err) {
        toast.error(getError(err));
      }
    };
    fetchCategories();
  }, []);
  return (
    <BrowserRouter>
    <div  className={
          sidebarIsOpen ? 'd-flex flex-column site-container active-cont' 
          : 'd-flex flex-column site-container'
        } >
     <ToastContainer position="bottom-center" limit={1}/>
      <header>
        <Navbar bg="dark" variant="dark">
          <Container>
          <Button
                variant="dark"
                onClick={() => setSidebarIsOpen(!sidebarIsOpen)}
              >
                <i className="fas fa-bars"></i>
              </Button>
            <LinkContainer to="/">
            <Navbar.Brand>Amazone</Navbar.Brand>
            </LinkContainer>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
              <Navbar.Collapse id="basic-navbar-nav">
                <SearchBox/>
            <Nav className="me-auto">
              <Link to="/cart" className="nav-link">
                Cart
                {
                  cart.cartItem.length >0 &&(
                    <Badge pill bg="danger">
                        {cart.cartItem.reduce((a,c)=> a+c.quantity,0)}
                    </Badge>
                  )
                }
              </Link>
              {userInfo ? (
                    <NavDropdown title={userInfo.name} id="basic-nav-dropdown">
                      <LinkContainer to="/profile">
                        <NavDropdown.Item>User Profile</NavDropdown.Item>
                      </LinkContainer>
                      <LinkContainer to="/orderhistory">
                        <NavDropdown.Item>Order History</NavDropdown.Item>
                      </LinkContainer>
                      <NavDropdown.Divider />
                      <Link
                        className="dropdown-item"
                        to="#signout"
                        onClick={signoutHandler}
                      >
                        Sign Out
                      </Link>
                    </NavDropdown>
                  ) : (
                    <Link className="nav-link" to="/signin">
                      Sign In
                    </Link>
                  )}
                  { userInfo && userInfo.isAdmin && (
                    <NavDropdown title="Admin" id="admin-nav-dropdown">
                      < LinkContainer to="/admin/dashboard">
                        <NavDropdown.Item>Dashboard</NavDropdown.Item>
                      </LinkContainer>
                      < LinkContainer to="admin/products">
                        <NavDropdown.Item>Products</NavDropdown.Item>
                      </LinkContainer>
                      < LinkContainer to="admin/orders">
                        <NavDropdown.Item>Orders</NavDropdown.Item>
                      </LinkContainer>
                      < LinkContainer to="/userlist">
                        <NavDropdown.Item>Users</NavDropdown.Item>
                      </LinkContainer>
                    </NavDropdown>
                  )

                  }
            </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>
      </header>
      <div
          className={
            sidebarIsOpen
              ? 'active-nav side-navbar d-flex justify-content-between flex-wrap flex-column'
              : 'side-navbar d-flex justify-content-between flex-wrap flex-column'
          }
        >
          <Nav className="flex-column text-white w-100 p-2">
            <Nav.Item>
              <strong>Categories</strong>
            </Nav.Item>
            {categories.map((category) => (
              <Nav.Item key={category}>
                <LinkContainer
                  to={`/search?category=${category}`}
                  onClick={() => setSidebarIsOpen(false)}
                >
                  <Nav.Link>{category}</Nav.Link>
                </LinkContainer>
              </Nav.Item>
            ))}
          </Nav>
        </div>
      <main className="mt-3">
        <Container>
        <Routes>
          <Route path="/product/:slug" element={<ProductScreen/>}/>

          {/* adimin Routes */}

          <Route path="/admin/dashboard" element={<AdminRoutes><DashboardScreen/></AdminRoutes>}/>
          <Route path="/admin/products" element={<AdminRoutes><ProductListScreen/></AdminRoutes>}/>
          <Route path="/admin/product/:id" element={<AdminRoutes><ProductEditScreen/></AdminRoutes>}/>
          <Route path="/admin/orders" element={<AdminRoutes><OrderListScreen/></AdminRoutes>}/>
          {/* <Route
          {/* <Route
              path="/product/:id"
              element={<ProductScreen />}
              exact
            ></Route> */}

          <Route path="/" element={<HomeScreen/>}/>
          <Route path="/cart" element={<ScreenCart/>}/>
          <Route path="/signin" element={<SigninScreen/>}/>
          <Route path="/signup" element={<SignupScreen/>}/>
          <Route path="/search" element={<SearchScreen/>}/>

          <Route path="/payment" element={<PaymentMethodScreen/>}/>
          <Route path="/placeorder" element={<PlaceOrderScreen/>}/>
          <Route path="/shipping" element={<ShoppingAddressScreen/>}/>
          <Route path="/order/:id" element={
           <ProtectedRoutes>
          <OrderScreen/>
         </ProtectedRoutes>}/>
          <Route path="/orderhistory" element={<OrderHistoryScreen/>}/>
          <Route path="/profile" element={
          <ProtectedRoutes>
            <ProfileScreen/>
          </ProtectedRoutes>
          }/>


          
        </Routes>
        </Container>
      </main>
      <footer>
      <div className="text-center"> All rights reserved</div>
      </footer>
    </div>
    </BrowserRouter>
  );
}

export default App;
